main{
    /* border: 1px solid rgb(0, 0, 0); */
    width: 850px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0%;
    transition: opacity 1.5s;
}

.hide-main {
    opacity: 0%;
}

.show-main {
    opacity: 100%;
}

@media (max-width:850px) {
    main {
        width: 100%;
    }
}

